import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import lista from './components/lista.vue'
import tiempo from './components/tiempo.vue'
import tokio from './components/tokio.vue'


Vue.config.productionTip = false

Vue.use(VueRouter)

const routes=[
  {path:'/tareas',component:lista},
  {path:'/noticias',component:tiempo},
  {path:'/tokio',component:tokio},
  {path:'/*',component:lista},
]
const router=new VueRouter({routes})
new Vue({
  render: h => h(App),
  router
}).$mount('#app')

