<template lang="html">

  <section class="lista">
    <h1>ProyectoVue.js-Adrián Contreras González</h1>
    <div class="container">
      <p>{{ completadas() }} tareas completadas de {{ todos.length }} tareas totales. <a v-on:click="borr" id="borrar"> x Borrar tareas completadas</a></p>
      
      <input v-model="ad" type="text" @keyup.enter='add' class="form-control" placeholder="Introduce tarea..">
      <br>
      <button class="btn bg-primary" v-on:click="add">add</button>
    </div>
      <div class="container1">
        <br>
        <ul>
          
            <li v-for="todo in todos" v-bind:key="todo.i">
              <div class="container2 text-left">
                <input  type="checkbox" v-model='todo.activar'>
                    <span id="nota" v-bind:class="{active: todo.activar}"> {{ todo.mensaje }} </span><br>
                    <span style="margin:10px">Priority: </span>
                    <div class="btn-group" role="group" aria-label="Basic example">
                      <button class="btn-secondary btn-success" v-on:click="prioridadLow(todo)" style="margin:6px">Low</button>
                      <button class="btn-secondary btn-warning" v-on:click="prioridadNormal(todo)" style="margin:6px">Medium</button>
                      <button class="btn-secondary btn-danger" v-on:click="prioridadHigh(todo)" style="margin:6px">High</button>
                    </div>
                    <span style="margin:10px"> Create: {{ todo.tiempo }} </span>
                    <button class="btn close" v-on:click="del(todo)">
                      <img src="../assets/basura.jpg" width="30px" height="30px">
                    </button>
              </div>
            </li>
          
        </ul>
      </div>
      <br>
  </section>

</template>

<script lang="js">

  export default  {
    name: 'lista',
    props: [],
    mounted () {
      if(localStorage.getItem("tareas")){
                    try {
                        this.todos=JSON.parse(localStorage.getItem("tareas"));
                    } catch (error) {
                        localStorage.removeItem("tareas");
                    }
                }
    },
    data () {
        return{
          todos:[],
          ad:"",
          prior:""
        }    
    },
    methods: {
      guardar:function(){
                    localStorage.setItem("tareas",JSON.stringify(this.todos));

                },
      add:function(){
        var hor=new Date();
        var hora=hor.getDate()+"/"+(hor.getMonth()+1)+"  "+hor.getHours()+":"+hor.getMinutes()+":"+hor.getSeconds();
        this.todos.push({mensaje:this.ad,activar:false,tiempo:hora,prioridad:this.prior});
        this.ad="";
        this.guardar();
      },
      del:function(nota){
        for (let i=0;i<this.todos.length;i++){
          if(this.todos[i]==nota){  
            this.todos.splice(i,1)
          }
        }
        this.guardar();
                },
      prioridadLow: function(todo){
        todo.prioridad="1";
      },
      prioridadNormal: function(todo){
        todo.prioridad="2";
      },
      prioridadHigh: function(todo){
        todo.prioridad="3";
      },          
      borr:function(){
                    for (let i=0;i<this.todos.length;i++){
                        if(this.todos[i].activar){  
                            this.todos.splice(i,1),
                            i--
                        }
                    }
                    this.guardar();
                },
      completadas:function(){
                    var comp=0;
                    for (let i in this.todos){
                        if(this.todos[i].activar){
                            comp++
                        }
                    }
                    return comp
                },
      orden:function(){
                    for (let i in this.todos){
                        this.todos[i].mensaje=this.aux[i]
                    }
                    
                }
    },
    computed: {

    }
}


</script>

