  .lista {

  }
  .active{
    text-decoration: line-through;
    color:lawngreen;
  }
  .container,.container1{
    border-bottom: 1px solid #333;
  }
  .container2{
    background-color: rgb(44, 49, 38);
    border:1px solid;
  }
  li{
    list-style:none;
  }
  li #nota{
    font-size: 25px;
