<template lang="html">

  <section class="tiempo">
    <h1>News</h1>
    <input v-model="ad" type="text" @keyup.enter='consultar' class="form-control" placeholder="¿Que le preocupa?">
  <div id="container" >
    <div v-for="noticia in noticias" v-bind:key="noticia.i" class="px-3">
      <div class="card" style="width: 18rem;">
        <img class="card-img-top" :src=noticia.imagen alt="Card image cap">
        <div class="card-body">
          <h5 class="card-title">{{noticia.titulo}}</h5>
          <p class="card-text">{{noticia.descrip}}</p>
        </div>
      </div>
    </div>
  </div>
  </section>

</template>

<script lang="js">
import axios from 'axios';
  export default  {
    name: 'tiempo',
    props: [],
    mounted () {
       
  },
    
    data () {
      return {
        noticias:[],
        ad:""
      }
    },
    methods: {
      consultar:function(){
        this.noticias=[];
        axios
      .get('https://newsapi.org/v2/everything?q='+this.ad+'&sortBy=publishedAt&apiKey=2ab87de608b34c87a2bb3fb19bd820a6')
      .then(response =>{
            for(let i=0;i<response.data.articles.length;i++){
              this.noticias.push({titulo:response.data.articles[i].title,imagen:response.data.articles[i].urlToImage,descrip:response.data.articles[i].description})
            }
          })
      },
    },
    computed: {

    }
}


</script>

